"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Image from "next/image";
import { Navbar as BSNavbar, Nav, Container, NavDropdown } from "react-bootstrap";

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);
  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 50);
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <BSNavbar
      sticky="top"
      expand="lg"
      className={`py-3 ${scrolled ? "navbar-scrolled" : "navbar-base"}`}
    >
      <Container>
        <Link href="/" className="navbar-brand d-flex align-items-center gap-2">
          <Image
            src="/images/logo_bloom_colores.png"
            alt="Bloom"
            className="logo-img"
            width={105}
            height={105}
            priority
          />
        </Link>

        <BSNavbar.Toggle />
        <BSNavbar.Collapse>
          <Nav className="ms-auto gap-2">
            <Link href="/nosotros" className="nav-link">Nosotros</Link>
            <Link href="/citas" className="nav-link">Citas</Link>
            <NavDropdown title="Servicios" id="servicios-dd">
              <Link href="/servicios/fisioterapia" className="dropdown-item">Fisioterapia</Link>
              <Link href="/servicios/estimulacion" className="dropdown-item">Estimulación</Link>
              <Link href="/servicios/preparacion-fisica" className="dropdown-item">Preparación física</Link>
            </NavDropdown>
          </Nav>
        </BSNavbar.Collapse>
      </Container>
    </BSNavbar>
  );
}
